   {
    size: 30,  //  30
    allowColor: "#ccc", // 
    allowType: '', // 
    arrow-up-bchevron-up""ios-arrow-up""ios-arrow-thin-up""android-arrow-up"
    allowImg: '' //  imgsrc
    bgColor: "#000", // 
    speed: 10 //   10
  }
-->
<template>
  <div class="gotop-box" v-show="status" :style="style" @click="gototop">
    <Icon
      v-if="!options.allowImg"
      :type="options.allowType"
      :size="options.size"
      :color="options.allowColor"
    ></Icon>
  </div>
</template>
<script>
export default {
  props: {
    options: {
      type: Object,
      default: function() {
        return {
          size: 100,
          allowColor: "#000",
          allowType: "ios-arrow-up",
          allowImg: require("@/assets/back_to_top.svg"),
          bgColor: "rgba(255,255,255,0)",
          speed: 10
        };
      }
    }
  },
  data() {
    return {
      style: {
        width: `${this.options.size / 50}rem`,
        height: `${this.options.size / 50}rem`,
        background: this.options.allowImg
          ? `url(${this.options.allowImg}) 0% 0% / 100% 100%`
          : this.options.bgColor
      },
      status: false,
      scrollTop: 0,
      timer: null
    };
  },
  mounted() {
    let _t = this;
    window.onscroll = function() {
      _t.scrollTop = document.body.scrollTop
        ? document.body.scrollTop
        : document.documentElement && document.documentElement.scrollTop
          ? document.documentElement.scrollTop
          : null;
      _t.status = _t.scrollTop && _t.scrollTop > 0;
    };
  },
  methods: {
    gototop() {
      let _t = this;
      _t.timer = setInterval(function() {
        _t.scrollTop -= 100;
        if (_t.scrollTop < 100) {
          _t.scrollTop = 0;
          _t.status = false;
          clearInterval(_t.timer);
        }
        scrollTo(0, _t.scrollTop);
      }, this.options.speed);
    }
  },
  destroyed() {
    clearInterval(this.timer);
  }
};
</script>
<style lang="stylus" scoped>
.gotop-box
  display flex
  justify-content center
  align-items center
  position fixed
  right 30px
  bottom 20px
  z-index 999
  opacity .75
  cursor pointer
  @media screen and (max-width : 560px)
    right 10px
</style>
